$메인칼라: black;

.wow-enter {
  opacity: 0;
}
.wow-enter-active {
  opacity: 1;
  transition: all 500ms;
}

.red {
  color: $메인칼라;
}

@mixin gkatn() {
  background-color: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  margin: auto;
  width: 100%;
}

.my-alert {
  @include gkatn();
}

.my-alert-red {
  @extend .my-alert;
  background-color: red;
  color: white;
}

.my-alert p {
  margin-bottom: 0;
}

